ly hot ground even further, doubling the pain for those damned here.  \n    ")]),_c('br'),_c('p',{attrs:{"id":"quote"}},[_c('i',[_vm._v("\"No green leaves in that forest, only black;"),_c('br'),_vm._v("\n      no branches straight and smooth, but knotted, gnarled;"),_c('br'),_vm._v("\n      no fruits were there, but briers bearing poison."),_c('br'),_vm._v("\n      Even those savage beasts that roam between"),_c('br'),_vm._v("\n      Cecina and Corneto, beasts that hate"),_c('br'),_vm._v("\n      tilled lands, do not have holts so harsh and dense\"")]),_c('br'),_vm._v("- Inferno")])]):_c('div',{key:"east"},[_c('p',[_c('v-btn',{on:{"click":_vm.toggleDiv}},[_vm._v("< West")])],1),_c('img',{attrs:{"src":require("..\\assets\\img\\7seventhCourt.jpg"),"alt":"Seventh Court"}}),_c('p',{staticClass:"westp"},[_vm._v("One of the notable punishments depicted in this scroll is the \"Terrace for viewing one's home village\".  \n      In this scroll, it is depicted as a punishment, where damned souls look back at the living world to \n      lament over what was lost to them.  One could see how they're descendants have little regard for them, or how\n      happy they are now that he/she is dead.  However, some interpretations of this terrace indicate it as a reward for \n      the living, in which those who've lived good lives have a opprotunity to see the world they left and how well \n      their family is doing.  In this interpretation, the deprivation of the terrace is infact the punishment for sinners.  \n      Similar to how Dante's interpretation of hell isn't the end all be all for how hell is percieved in the Western or \n      Christian world, the same is true for these hell scrolls, or the story of Mulian's journey thorugh hell.\n    ")]),_c('img',{attrs:{"id":"terrace","src":require("..\\assets\\img\\7terrace.jpg"),"alt":"Terrace For Viewing One's Home Village"}}),_c('p',{attrs:{"id":"quote"}},[_c('i',[_vm._v("\"It’s hard to enjoy your prime years again;"),_c('br'),_vm.